<template>
  <div class="landing">
    <div class="landing-text">
      <!-- logo -->
      <!-- 注册 -->
      <div class="regegit">
        <van-cell-group :maxlength="12">
          <van-field
            v-model="user.value"
            left-icon="contact"
            class="animated zoomInDown"
            right-icon
            :error-message="user.errorMessage"
            @input="validUP(user.value, 'validUsername', {key: 'user', errorMessage: '账号支持4-8个(字母数字_)组合，必须字母开头'})"
            placeholder="账号"
          />
          <van-field
            v-model="pwd1.value"
            :type="pwd1.type"
            left-icon="bag-o"
            class="animated zoomInDown"
            :error-message="pwd1.errorMessage"
            :right-icon="pwd1.icon"
            color="white"
            @click-right-icon="checkPassword('pwd1')"
            @input="validUP(pwd1.value, 'validPwd', {key: 'pwd1', errorMessage: '密码支持6-16个(字母数字_)组合，必须含有大或小字母'})"
            placeholder="密码"
          />
          <van-field
            v-model="pwd2.value"
            :type="pwd2.type"
            left-icon="bag-o"
            class="animated zoomInDown"
            :error-message="pwd2.errorMessage"
            :right-icon="pwd2.icon"
            color="white"
            @click-right-icon="checkPassword('pwd2')"
            @input="validEqual(pwd1.value, pwd2.value)"
            placeholder="再次输入密码"
          />
        </van-cell-group>
        <div class="btn">
          <van-button
            icon="https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1627475845,3049836284&fm=26&gp=0.jpg"
            round
            type="info"
            size="20"
            class="animated zoomInUp"
            @click="bthding()"
            :loading="isLoading"
            color="linear-gradient(to right, red, #B02125)"
            loading-text="注册中..."
            :disabled="!isValid"
          >点击注册</van-button>
        </div>
      </div>
    </div>
    <div class="chuce animated bounceInDown">
      点击注册，表示您已经同意相关协议
      <strong>|</strong>
      <span class="animated bounceInDown" @click="push({name:'register'})">登录</span>
    </div>
  </div>
</template>

<script>
import { validForm } from "../../tool/validForm";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("registerModule");
export default {
  data() {
    return {};
  },
  computed: {
    ...mapState(["pwd1", "pwd2", "isLoading", "isValid", "user"])
  },
  methods: {
    ...mapMutations(["checkPassword"]),
    //注册区域
    bthding() {
      //控制注册按钮状态
      this.reloading = true;

      let self = this;
      //注册
      this.$store.commit("registerModule/register", true);
      let randomTime = Math.random() * 5 * 1000;
      setTimeout(function() {
        //获取所有用户信息
        let users = localStorage.getItem("users");
        users = users ? JSON.parse(users) : [];
      
        //判断用户是否存在
        for (let i = 0; i < users.length; i++) {
          if (users[i].username == self.user.value) {
            self.$toast({
              duration: 2000,
              message: "该用户已存在"
            });
            self.$store.commit("registerModule/register", false);
            return;
          }
        }
        self.$toast({
          duration: 2000,
          message: "注册成功"
        });
        self.$store.commit("registerModule/register", false);
        //时间
        let time = new Date().getTime();
        //记录用户信息
        let userInformation = {
          //账号
          username: self.user.value,
          //密码
          pwd: self.pwd1.value,
          //性别
          sex:'kkk',
          //注册时间
          time
        };
        //将用户信息储存进本地储存
        users.push(userInformation);
        localStorage.setItem("users", JSON.stringify(users));
         //跳转到登录页面
        self.$router.push({name: 'register'});
      },randomTime);
    },
    //验证用户名
    validUsername(value) {
      let isValid = ValidForm.validUsername(value);
      this.$store.commit("registerModule/validUsername", isValid);
    },
    //验证密码
    validPwd(value) {
      let isValid = validForm.validPwd(value);
      this.$store.commit("registerModule/validPwd", isValid);
    },
    //验证两值是否相等
    validEqual(v1, v2) {
      let isValid = validForm.validEqual(v1, v2);

      this.$store.commit("registerModule/validEqual", isValid);

      this.$store.commit("registerModule/passForm");
    },
    //验证用户名或者密码
    validUP(value, fnName, o) {
      let isValid = validForm[fnName](value);

      o.isValid = isValid;

      this.$store.commit("registerModule/validUP", o);

      this.$store.commit("registerModule/passForm");
    },
    //登录
    push(o) {
      this.$router.push(o);
    }
  },
  components: {},
  created() {
   
  }
};
</script>
<style lang="less">
.notify {
  font-size: 12px;
  height: 35px;
  position: fixed;
  bottom: 0px;
}
.van-hairline--top-bottom::after, .van-hairline-unset--top-bottom::after{
  border-width: 0;
}
.van-field__left-icon .van-icon,
.van-field__right-icon .van-icon {
  color: black;
}
.landing {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: url("../../image/背景.png") no-repeat center center;
  background-size: 100% 100%;

  .landing-text {
    padding: 0 50px;
    margin-top: 50%;
    
    .regegit {
      overflow: hidden;
      position: relative;
      .van-cell-group {
        background-color: transparent;

        .van-field__left-icon {
          margin-right: 10px;
        }
        .van-cell {
          background-color: rgba(0, 0, 0, 0.2);
          margin-bottom: 10px;
          border-radius: 20px;
        }
      }
      .btn {
        margin-top: 15px;
        button {
          padding: 0 80px;
          display: block;
          margin: 0 auto;
          box-shadow: 0px 1px 4px 0px black;
        }
      }
    }
  }
  .chuce {
    width: 100%;
    line-height: 30px;
    font-size: 12px;
    text-align: center;
    position: absolute;
    color: white;
    left: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.2);
    strong {
      margin: 0 5px;
    }
    span {
      color: black;
    }
  }
}
</style>